<script>
import { mapGetters } from 'vuex'

export default {
    name: 'Stock',
    data () {
      return {
        threeFrame: ['3minAbove', '3minBelow', '3minIn'],
        fiveFrame: ['5minAbove', '5minBelow', '5minIn'],
        fifteenFrame: ['15Above', '15Below', '15In'],
        thirtyFrame: ['30Above', '30Below', '30In'],
        hourFrame: ['HourAbove', 'HourBelow', 'HourIn'],
        dayFrame: ['dailyAbove', 'dailyBelow', 'dailyIn'],
        stock: '',
        itemsPerPageArray: [4, 8, 12],
        search: '',
        filter: {},
        sortDesc: false,
        page: 1,
        itemsPerPage: 4,
        sortBy: 'name',
        
      }
    },
    created() {
        // console.log(this.$route.params.id);
    },
    computed: {
        ...mapGetters(['timeframes']),

        stockFilter() { 
            var stock = this.$route.params.id;
            var frames = []
            // console.log(stock)
            // console.log(Object.keys(this.timeframes))
            var result = Object.entries(this.timeframes)
            for (var i = 0; i < result.length; i++) {
                var arrayedObj = Object.keys(result[i][1])
                // console.log(result[i][0])
                for (var j = 0; j < arrayedObj.length; j++) {
                    // console.log(arrayedObj[j])
                    // var exact = stock 
                    // if (arrayedObj[j].includes(stock)) {
                    if (arrayedObj[j].includes(stock) && arrayedObj[j].length == stock.length) {
                        // console.log(arrayedObj[j].length)
                        // console.log(stock.length)
                        // console.log(result[i][0] + '-' + result[i][1] + ':' +[arrayedObj[j]])
                        var title = result[i][0]
                        var each_frame = result[i][1][arrayedObj[j]]

                        frames.push({title, each_frame})
                        // console.log(title, stock, ': ', each_frame)
                    }
                }
            }
            console.log(frames)
            return frames
        },
        numberOfPages () {
            return Math.ceil(this.items.length / this.itemsPerPage)
        },
        filteredKeys () {
            return this.keys.filter(key => key !== 'Name')
        },
        
        
    },
    filters: {
      toCurrency(amount) {
        var formatter = new Intl.NumberFormat('en-US', {
          style: 'currency',
          currency: 'USD',
        });
        return formatter.format(amount)
    }
  },
    methods: {
      cloudLocation(title) {
          // console.log(title.includes('Below'))
          if (title.includes('Below')) {
            return 'Below'
          } else if (title.includes('Above')) {
            return 'Above'
          } else if (title.includes('In')) {
            return 'In'
          }
      },
      threeCheck (stockFilter) {
          if (this.threeFrame.includes(stockFilter.title)) {
            return true
          }
          console.log(this.threeFrame.includes(stockFilter.title))
          return stockFilter.title
        },
      nextPage () {
        if (this.page + 1 <= this.numberOfPages) this.page += 1
      },
      formerPage () {
        if (this.page - 1 >= 1) this.page -= 1
      },
      updateItemsPerPage (number) {
        this.itemsPerPage = number
      },
    },

}
</script>

